import React, { Component } from 'react';
import {Link} from "react-router-dom";
// import {EditProd} from "./EditProd";
//
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import {Button} from '@material-ui/core';

//
import { getFakeAPI } from '../Services/fakeAPI';
import {delProduct, updateData, editUsers} from "../Services/fakeAPI";

export class Products extends Component {
    constructor(props){
        super(props);
        this.state = {getAPIData:[]}
    }

    componentDidMount(){
        const getFakeData = async() =>{
            let resp = await getFakeAPI();
            console.log("Fake API data===>",resp);
            this.setState({getAPIData: resp.data});
        }
        getFakeData();
    }
    //
    delProd = (id)=>{
        // alert(id);
       if(window.confirm("Do u want to delete this product ?")){
        delProduct(id)
        .then(res=>{
            alert("Product Deleted !!");
            getFakeAPI()
            .then(res=>{
                this.setState({getAPIData: res.data})
            })
        })
       }
    }
    //
    userData = (id)=>{
        console.warn("state data",this.state.id);
    }
    render() {
        return (
        <div>
                <h2>Products..</h2>
                <br /><br />
                <Grid container justifyContent="center" spacing={4}>
                {this.state.getAPIData.map((items)=>{
                    return(
                        <Card sx={{ maxWidth: 345 }} style={{margin:"10px"}} key={items.id}>
                        <CardHeader
                            title={items.pname}
                            subheader={items.desc}
                        />
                        <CardMedia
                            component="img"
                            height="194"
                            image={items.img1}
                            alt="image 1"
                        />
                        <CardContent>
                            <Typography variant="body2" color="text.secondary">{items.quant}</Typography>
                            <Typography >${items.price}</Typography>
                            <hr />

                            <Link to={{pathname: `/editprods/:id`, state:{IDdata: this.state} }}>
                            <Button variant="outlined" color="primary">Edit</Button>
                            </Link>&nbsp;&nbsp;
                            <Button variant="outlined" color="secondary" onClick={()=>this.delProd(items.id)}>Delete</Button>
                        </CardContent>
                    </Card>
                )                
    })}
         </Grid>   
    </div>
        )
    }
}

export default Products;

