import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Routes} from "react-router-dom";
import AddProds from './AddProds';
import Navbars from './Navbars';
import Users from "./Users";
import NotFound from "./NotFound";
import Home from './Home';
import Products from './Products';
import EditProd from './EditProd';
import { getFakeAPI } from '../Services/fakeAPI';



export class Landing extends Component {
    constructor(props){
        super(props);
        this.sate = {getAPIData:[]}
    }
    componentDidMount(){
        const getFakeData = async() =>{
            let resp = await getFakeAPI();
            console.log("Fake API data===>",resp);
            this.setState({getAPIData: resp.data});
        }
        getFakeData();
    }

    
    render() {
        return (
           <div>
               <Router>
                   <Navbars />
                   <Routes>
                       <Route path="/" element={<Home />} /> 
                       <Route path="/users" element={<Users />} />
                       <Route path="/prods" element={<Products />} />
                       <Route path="/addprods" element={<AddProds />} />
                       <Route path="/editprods/:id" element={<EditProd />} />
                       <Route path="/*" element={<NotFound />} />
                   </Routes>
               </Router>
           </div>
        )
    }
}

export default Landing;
