import React, { Component } from 'react';
import { Button} from '@mui/material';
import { TextField } from '@material-ui/core';
import {Form} from "react-bootstrap";
import { updateData } from '../Services/fakeAPI';

export class EditProd extends Component {
    constructor(props){
        super(props);
        // const {state} = this.state;
        const {id, pname, price, desc, quant, img1} = this.props.state.IDdata;
        this.state = {id:id, pname:pname, price:price, desc:desc, quant:quant, img1:img1};
        console.warn("id",id);
        
    }


    componentDidMount(){
        updateData()
        .then(res =>{
            this.setState(res.data);
            console.log("res in edit===>",this.state)
        })
    }

    selectUser = (id) =>{
        

    }
   
    
    
    render() {
      
        return (
            <div>
                <Form className="form1">      
                    <h2>Edit Product</h2>  
                    <hr />
                    <br />            
                    <TextField style={{width:"30rem"}} name="pname" value={this.state.pname} label="Name" id="outlined-basic" variant="outlined" onChange={this.changeHandler}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="price" value={this.state.price}  label="Price" id="outlined-basic" variant="outlined" onChange={this.changeHandler}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="desc" value={this.state.desc} label="Description" id="outlined-basic" variant="outlined" onChange={this.changeHandler}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="quant" value={this.state.quant} label="Quantity" id="outlined-basic" variant="outlined" onChange={this.changeHandler}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="img1"value={this.state.img1} id="outlined-basic" label="Image Path" variant="outlined" onChange={this.changeHandler}/>
                    <br /><br />
                    <Button style={{width:"15rem", height:"3rem", marginTop:"8px"}} variant="contained" type='submit' onClick={this.selectUser(this.state.id)}>Edit</Button>
                    {/* <input type="submit" value="submit" className='btn btn-success' /> */}
            </Form>
            </div>
        )
    }
}

export default EditProd;
