import React, { Component } from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';

import {Link} from "react-router-dom";

const myStyles = {
    color:"white",
    margin:"20px",
    fontSize:"1.7rem",
    fontWeight:"bold",
    
    appBar:{
        height:"90px",
        padding:"10px",
        
    }
}
export class Navbars extends Component {
    render() {
        return (
            <div>
                <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static" color="primary" style={myStyles.appBar}>
                    <Toolbar>
                        <Link variant="h5" component="div" style={myStyles} to="/">Home</Link>
                        <Link variant="h5" component="div" style={myStyles} to="/users">Users</Link>
                        <Link variant="h5" component="div" style={myStyles} to="/prods">Products</Link>
                        <Link variant="h5" component="div" style={myStyles} to="/addprods">Add Products</Link>
                    </Toolbar>
                </AppBar>
                </Box>
            </div>
        )
    }
}

export default Navbars;
