import React, { Component } from 'react';
import { Button} from '@mui/material';
import { TextField } from '@material-ui/core';
import {Form} from "react-bootstrap";


import "./addprods.css";
import { postData } from '../Services/fakeAPI';

export class AddProds extends Component {
    constructor(props){
        super(props);
        this.state={pname:"", price:"",desc:"", quant:"", img1:""};
        const {history} = this.props;
    }

    handleInp = (e) =>{
        const {name, value} = e.target;
        console.warn("e.target==>",e.target.value);
        this.setState({[name]:value});
    }

    //
    addData = (event) =>{
        event.preventDefault();
        // alert("JK");
        console.warn(this.state);

        postData(this.state).then(res=>{
            if(res){
                alert("Product Added !!");
            }            
        })
        .then(()=>{
            this.history.push("/prods");
        })
        
    }
    
    render() {
        return (
            <div>
            <Form className="form1" onSubmit={this.addData}>      
            <h2>Add Product's</h2>  
            <hr />
            <br />            
                    <TextField style={{width:"30rem"}} name="pname" label="Name" id="outlined-basic" variant="outlined"  onChange={this.handleInp}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="price" label="Price" id="outlined-basic" variant="outlined"   onChange={this.handleInp}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="desc" label="Description" id="outlined-basic" variant="outlined"   onChange={this.handleInp}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="quant" label="Quantity" id="outlined-basic" variant="outlined"   onChange={this.handleInp}/>
                    <br /><br />
                    <TextField style={{width:"30rem"}} name="img1" id="outlined-basic" label="Image Path" variant="outlined"  onChange={this.handleInp}/>
                    <br /><br />
                    <Button style={{width:"15rem", height:"3rem", marginTop:"8px"}} variant="contained" type='submit'>Add</Button>
                    {/* <input type="submit" value="submit" className='btn btn-success' /> */}
            </Form>
        </div>
        )
    }
}

export default AddProds
